import React, { useState, useContext } from "react";

import SearchStation from "./SearchStation";
import GetStation from "./GetStation";
import Geolocation from "./Geolocation";

import { StationContex } from "../StationContex";

const Stations = () => {
  const allStation = useContext(StationContex);
  const [city, setCity] = useState([]);

  const searchHandler = value => {
    const searchCity = allStation.filter(item => {
      if (item.city !== null) {
        return item.city.name.toLowerCase() === value.toLowerCase();
      } else {
        return null;
      }
    });

    if (searchCity.length > 0) {
      setCity(searchCity);
    } else {
      setCity([
        {
          key: "empty",
          name:
            "I don't have this place in the database, try to enter another place or go back to the nearest measuring stations"
        }
      ]);
    }
  };

  return (
    <div className="container stations">
      <div className="row no-gutters">
        <div className="col">
          <SearchStation search={searchHandler} />
          {city.length > 0 ? (
            <div className="container">
              <div className="row no-gutters">
                {city[0].key === "empty" ? (
                  <div>
                    <h2 className="subtitle">{city[0].name}</h2>
                    <button onClick={() => setCity([])} className="button-info">
                      Back
                    </button>
                  </div>
                ) : (
                  city.map(item => <GetStation key={item.id} item={item} />)
                )}
              </div>
            </div>
          ) : (
            <Geolocation />
          )}
        </div>
      </div>
    </div>
  );
};

export default Stations;
